.react-draggable {
    // display: inline-block;
    position: absolute;
    z-index: inherit;
}

.layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
    z-index: 20000 !important;
}

.fullscreen.initial.fade {
    pointer-events: none;
}

.ril-image-current {
    max-height: 100%;
}

.Toastify {
    .Toastify__toast-container--bottom-right {
        bottom: 4em;
    }

    .Toastify__progress-bar--dark {
        background-color: _palette(accent);
    }

    .Toastify__toast--dark {
        background-color: #2c2c2c;
    }
}

ul.MuiList-root.MuiMenu-list {
    min-width: 150px;

    .MuiMenuItem-root.MuiListItem-button.important {
        color: rgb(206, 0, 0);
    }

    .MuiMenuItem-root.MuiListItem-button.disabled {
        text-decoration: line-through;
        text-decoration-color: rgba(255, 255, 255, 0.5);
    }

    .MuiMenuItem-root.MuiListItem-button.un-selectable {
        opacity: 0.5;
        text-decoration-color: rgba(255, 255, 255, 0.5);
        place-content: center;
    }

    hr {
        border-color: rgba(150, 150, 150, 0.5);
        margin: 0.25em 0;
    }
}

.chakra-menu__menuitem.disabled {
    text-decoration: line-through;
}
